<template>
    <!-- 头部 -->
    <div class="head-content">
        <div class="head-left">
            <div class="user">
                <img src="../../../../assets/ico/icon_saleman2.png" alt="" class="user-avatar">
            </div>
            <div class="user-message">
                <ul>
                    <li>{{$store.getters.getUserinfo.fullname}}，欢迎回来！</li>
                    <li class="color-7" v-if="isexist">工号:{{jobnumber}}，{{position}}</li>
                    <li class="color-7">{{newsnumber}}条<span class="color-1">新通知 </span>| <span class="color-1">安全设置</span></li>
                </ul>
            </div>
        </div>
        <div class="head-right">
            <div class="weather-message">
                <ul>
                    <li>{{weatherData.city_name}}</li>
                    <li>{{weatherData.text}}{{weatherData.temperature}}℃</li>
                    <li>{{weatherData.date}}</li>
                </ul>
            </div>
            <div class="weather">
                <div :class="weatherData.iconClass"></div>
                <!--<img src="../../../../assets/ico/icon_saleman2.png" alt="" class="user-avatar">-->
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            isexist:true,
            jobnumber:null,
            newsnumber:this.$store.state.noticnum ||0,
            weatherData: {
                city_name: '南海',
                text: '晴',
                temperature: 15,
                date: null,
                iconClass : 'sunny'//sunny//cloudy//rainy//rainbow//starry//stormy//snowy
            }
        }
    },

    props:['position'],
    mounted() {
        this.init();
    },
    watch: {
        //"$store.state.noticnum":"getnumber"
    },
    methods: {
        init() {
            //this.initEchart();
            //this.setEchart();
            this.getlist();
            //this.getWeatherData();
        },

        getlist(){
            this.fetch('/sys/user/getAll', this.objfilter({
                account: this.$store.getters.getUserinfo.account,
                pageNo: 1,
                pageSize: 10
            })).then((result) => {
                if (result.status === 200) {
                    if(result.data.list.length > 0){
                        if(result.data.list[0].empCode){
                            this.jobnumber = result.data.list[0].empCode;
                        }else{
                            this.isexist = false;
                        }
                    }else{
                        this.isexist = false;
                    }
                }
            })
        },

        //设置消息数量
        getnumber(){
            this.newsnumber = sessionStorage.getItem('newsnumber');
        },

        //获取天气
        getWeatherData() {
            let date = new Date();
            this.weatherData.date = `${date.getFullYear()}年${date.getMonth()+1}月${date.getDate()}日`;
            this.$http.get('/all?latitude=0&longitude=0&isLocated=true&locationKey=weathercn%3A101280803&appKey=weather20151024&sign=zUFJoAR2ZVrDy1vF3D07&isGlobal=false&locale=zh_cn', {baseURL: '/weather'})
                .then(result => {
                    if(result.status === 200 && result.data) {
                        console.log('weather',result.data.current);
                        this.weatherData.temperature = result.data.current.temperature.value;
                        this.weatherData.iconClass = this.matchWeatherIcon(result.data.current.weather);
                        this.weatherData.text = this.matchWeatherText(result.data.current.weather);
                    }else {
                        this.weatherData.city_name = '南海';
                        this.weatherData.text = '多云';
                        this.weatherData.temperature = 20;
                        this.weatherData.iconClass = 'cloudy';
                    }
                })
        },

        //天气图标
        matchWeatherIcon(val) {
            if(val == 0) {
                return 'sunny'
            }else if(val == 1) {
                return 'cloudy'
            }else if([2,3,4,5,7,8,9,10,11,12,21,22,23,24,25].includes(val)) {
                return 'rainy'
            }else if([18,19,35,53 ].includes(val)) {
                return 'starry'
            }else if([20,29,30,31,32,33],includes(val)) {
                return 'stormy'
            }else if([6,13,14,15,16,17,26,27,28,34].includes(val)) {
                return 'snowy'
            }else{
                return 'rainbow'
            }
        },
        //天气文字
        matchWeatherText(val) {
            let text = '';
            const matchList =
                [
                    {
                        "code": 0,
                        "wea": "晴"
                    },
                    {
                        "code": 1,
                        "wea": "多云"
                    },
                    {
                        "code": 2,
                        "wea": "阴"
                    },
                    {
                        "code": 3,
                        "wea": "阵雨"
                    },
                    {
                        "code": 4,
                        "wea": "雷阵雨"
                    },
                    {
                        "code": 5,
                        "wea": "雷阵雨并伴有冰雹"
                    },
                    {
                        "code": 6,
                        "wea": "雨夹雪"
                    },
                    {
                        "code": 7,
                        "wea": "小雨"
                    },
                    {
                        "code": 8,
                        "wea": "中雨"
                    },
                    {
                        "code": 9,
                        "wea": "大雨"
                    },
                    {
                        "code": 10,
                        "wea": "暴雨"
                    },
                    {
                        "code": 11,
                        "wea": "大暴雨"
                    },
                    {
                        "code": 12,
                        "wea": "特大暴雨"
                    },
                    {
                        "code": 13,
                        "wea": "阵雪"
                    },
                    {
                        "code": 14,
                        "wea": "小雪"
                    },
                    {
                        "code": 15,
                        "wea": "中雪"
                    },
                    {
                        "code": 16,
                        "wea": "大雪"
                    },
                    {
                        "code": 17,
                        "wea": "暴雪"
                    },
                    {
                        "code": 18,
                        "wea": "雾"
                    },
                    {
                        "code": 19,
                        "wea": "冻雨"
                    },
                    {
                        "code": 20,
                        "wea": "沙尘暴"
                    },
                    {
                        "code": 21,
                        "wea": "小雨-中雨"
                    },
                    {
                        "code": 22,
                        "wea": "中雨-大雨"
                    },
                    {
                        "code": 23,
                        "wea": "大雨-暴雨"
                    },
                    {
                        "code": 24,
                        "wea": "暴雨-大暴雨"
                    },
                    {
                        "code": 25,
                        "wea": "大暴雨-特大暴雨"
                    },
                    {
                        "code": 26,
                        "wea": "小雪-中雪"
                    },
                    {
                        "code": 27,
                        "wea": "中雪-大雪"
                    },
                    {
                        "code": 28,
                        "wea": "大雪-暴雪"
                    },
                    {
                        "code": 29,
                        "wea": "浮沉"
                    },
                    {
                        "code": 30,
                        "wea": "扬沙"
                    },
                    {
                        "code": 31,
                        "wea": "强沙尘暴"
                    },
                    {
                        "code": 32,
                        "wea": "飑"
                    },
                    {
                        "code": 33,
                        "wea": "龙卷风"
                    },
                    {
                        "code": 34,
                        "wea": "若高吹雪"
                    },
                    {
                        "code": 35,
                        "wea": "轻雾"
                    },
                    {
                        "code": 53,
                        "wea": "霾"
                    },
                    {
                        "code": 99,
                        "wea": "未知"
                    }
                ];
            let textArr = matchList.filter(v => v.code == val) || [{code:null,wea:null}];
            text = textArr[0].wea;
            return text
        }
     }
}
</script>

<style scoped lang="less" type="text/less">
    .head-content {
        display: flex;
        width: 100%;
        height: 90px;
        padding-bottom: 12px;
        align-items: center;
        .head-left {
            flex: 2;
            justify-content: flex-start;
            display: flex;
            .user {
                .user-avatar {
                    width: 80px;
                    height: 80px;
                    border-radius: 50%;
                    border: 1px solid #ccc;
                }
             }
             .user-message {
                 margin-left: 10px;
                 display: flex;
                 align-items: center;
                 li:nth-of-type(1) {
                     font-size: 24px;
                     font-weight: 700;
                 }
            }
        }
        .head-right {
            flex: 1;
            justify-content: flex-end;
            display: flex;
            position: relative;
            .weather-message {
                display: flex;
                align-items: center;
                margin-right: 80px;
                ul {
                    text-align: right;
                    li {
                        color: #888888;
                    }
                }
            }
            .weather {
                position: relative;
                margin-right: 80px;
                margin-bottom: 20px;
                z-index: 99;
            }
        }
    }

</style>



